import { db } from "./firestore";
import { doc, runTransaction } from "firebase/firestore";

async function vote(survey, user, choice) {
  /*    */

  const response = {
    ok: false,
    message: null
  }

  if (!(survey && user && choice)) {
    response.message = "   .";
    return response;
  }

  if (!user.profile) {
    response.message = "  .";
    alert("    .");
    window.location.assign("/editprofile");
    return response;
  }

  try {
    const profile = user.profile;
    const surveyRef = doc(db, "Survey", survey.sid); //  
    const userRef = doc(db, "Users", user.uid); //  

    await runTransaction(db, async (transaction) => {
      //  
      const surveyDoc = await transaction.get(surveyRef);
      if (!surveyDoc.exists()) {
        throw new Error("   .");
      }

      const oldData = surveyDoc.data().stats;
      const newData = {}
      newData[`stats.total`] = oldData.total + 1; //   1 
      newData[`stats.${choice}.total`] = oldData[choice].total + 1; //    1 
      newData[`stats.${choice}.MBTI.${profile.MBTI}`] = oldData[choice].MBTI[profile.MBTI] + 1;
      newData[`stats.${choice}.${profile.gender}.${profile.age}`] = oldData[choice][profile.gender][profile.age] + 1;
      console.log("newData", newData)

      //     
      const { comment, stats, ...partSurvey } = survey;
      const userSurveyData = {
        ...partSurvey,
        isChosen: choice,
        isVoted: true
      }

      const userDoc = await transaction.get(userRef);
      const userSurvey = userDoc.data().survey;
      console.log("userSurvey", userSurvey)

      if (Object.keys(userSurvey).includes(survey.sid)) {
        throw new Error("  .");
      }
      if (!userSurvey) {
        throw new Error("  .");
      } else if (typeof userSurvey === "object") {
        userSurvey[survey.sid] = userSurveyData;
        //  
        transaction.update(surveyRef, newData);
        transaction.update(userRef, { survey: userSurvey });
      } else {
        throw new Error(" .");
      }

      response.ok = true;
      response.survey = userSurveyData;

      return response;

    })
  } catch (err) {
    console.log(err);
    response.message = err.message;

    return response;
  }

  return;
}

export default vote;