import { userCol } from "./firestore";
import { doc, setDoc } from "firebase/firestore";

async function createUser(data) {
  /*    */

  const response = {
    ok: false,
    message: null
  }

  /*   */
  if (!data) {
    //  undefined null  
    response.message = "   .";
    return response;
  } else {
    //     undefined, null  
    const { id, password, question, answer } = data;
    if (!(id && password && question && answer)) {
      response.message = " ,   ."
      return response;
    }
  }

  /*  */
  try {
    const userRef = doc(userCol); //   id  

    await setDoc(userRef, {
      ...data,
      survey: {},
      uid: userRef.id // uid      
    })

    response.ok = true;
    response.user = {
      ...data,
      uid: userRef.id
    }
    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default createUser;