import { db } from "./firestore";
import { doc, updateDoc } from "firebase/firestore";

async function setProfile(uid, data) {
  /*   */

  const response = {
    ok: false,
    message: null
  }

  /*   
             */
  const genderList = ["", ""];
  const ageList = ["10", "20", "30", "40", ""];
  const MBTIList = ["ESTJ", "ESTP", "ESFJ", "ESFP", "ENTJ", "ENTP", "ENFJ", "ENFP", "INFP", "INFJ", "INTP", "INTJ", "ISFP", "ISFJ", "ISTP", "ISTJ"];


  /*   */
  if (!(uid && data)) {
    response.message = "  ,    .";
    return response;
  }

  const { gender, age, MBTI, profileImage } = data;
  if (!(genderList.includes(gender) && ageList.includes(age) && MBTIList.includes(MBTI))) {
    response.message = ", , MBTI    ."
    return response;
  }


  try {
    const userRef = doc(db, "Users", uid); //   id  

    await updateDoc(userRef, {
      profile: data
    })

    response.profile = data;
    response.ok = true;

    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default setProfile;