import { db } from "./firestore";
import { doc, getDoc } from "firebase/firestore";

async function getUserData(uid) {
  /*  ID     */

  const docRef = doc(db, "Users", uid);
  const docSnap = await getDoc(docRef);
  if (docSnap?.exists()) {
    return docSnap.data();
  } else {
    throw new Error("  .");
  }

}

export default getUserData;