import checkId from "./checkId";

async function login(id, password) {
  /*  
           return */

  const response = {
    ok: false,
    message: null,
  }

  try {
    const res = await checkId(id);

    if (!res) { //    
      response.message = "  ID.";
    }
    else if (res.password !== password) { //      
      response.message = " .";
    }
    else if (res.password === password) { //        
      response.ok = true;
      response.user = res;
    }

    return response;

  } catch (error) {
    console.log(error);
    response.message = error.message;

    return response;
  }
}

export default login;