import { db } from "./firestore";
import { doc, getDoc } from "firebase/firestore";

async function getSurvey(surveyId) {
  /*  ID     
       ..? */

  const docRef = doc(db, "Survey", surveyId);
  const docSnap = await getDoc(docRef);
  if (docSnap?.exists()) {
    return docSnap.data();
  } else {
    throw new Error("  .");
  }
}

export default getSurvey;