import "./SignUpPage.css";
import Button from "../../components/Button/Button";
import { useState } from "react";
import SignUpInput from "../../components/SignUp/SignUpInput";
import useSignUp from "../../hooks/useSignUp";

function SignUpPage() {
  const [inputs, setInputs] = useState({
    id: "",
    password: "",
    passwordCheck: "",
    question: "  1?",
    answer: "",
  });

  //   
  const questions = ["  1?", "  ?", "   ?", " ?", "    ?"];

  const { signUp, isPending, error } = useSignUp();

  const inputHandler = (event) => {
    setInputs({
      ...inputs,
      [event.target.name]: event.target.value
    })
  }

  const submitHandler = async (event) => {
    //
    const { passwordCheck, ...userData } = inputs;

    const res = await signUp(userData);
    console.log(res);
  }

  return (
    <div className="sign-up-wrapper">
      <h2> </h2>
      <form onChange={inputHandler}>
        <SignUpInput id="sign-up-id" name="id" type="text" label="" />
        <SignUpInput id="sign-up-password" name="password" type="password" label="" />
        <SignUpInput id="sign-up-password-check" name="passwordCheck" type="password" label=" " />
        <label htmlFor="sign-up-question"></label>
        <select id="sign-up-question" name="question">
          {questions.map((question) => <option value={question}>{question}</option>)}
        </select>
        <input id="sign-up-answer" name="answer" type="text" />
        <p className="sign-up-error-message"></p>
        <Button size="small"
          background="var(--color-button-a)"
          padding="1rem 3rem"
          margin="1.6rem 0"
          text=""
          disabled={isPending}
          onClick={submitHandler} />
      </form>
    </div>
  )
}

export default SignUpPage;